<template>
  <div class="wrapper">
    <div class="w-seach" @click="handlePopShow">
      <p :title="curName.length>6?curName:''">{{curName}}</p>
    </div>
    <transition name="fade">
      <depSeach v-if="popShow" :name="curName" @change="popClick"></depSeach>
    </transition>
    <div class="content">
      <div class='c-left'>
        <ctitle :title="'参评情况'"></ctitle>
        <div class='cpl'>
          <liquidfill :params='cplParams'></liquidfill>
          <div class='cpl-font'>参评率</div>
        </div>
        <div class='cpl cpl3d'>
          <pie-chart3D :params="cpqkPieParams"></pie-chart3D>
          <!-- <customized-pie :params="cpqkPieParams"></customized-pie> -->
        </div>
      </div>
      <div class='c-center'>
        <ctitle :title="'评价情况'"></ctitle>
        <div class='cpl'>
          <liquidfill :params='mylParams'></liquidfill>
          <div class='cpl-font'>满意率</div>
        </div>
        <div class='cpl cpl3d'>
          <pie-chart3D :params="pjqkPieParams"></pie-chart3D>
          <!-- <customized-pie :params="pjqkPieParams"></customized-pie> -->
        </div>
      </div>
      <div class='c-right'>
        <ctitle :title="'信访部门满意率排名TOP10'"></ctitle>
        <div class='cpl'>
          <div class="w-echart" id="w-echart" ref="barEl">
            <PaimBar :params="sjxfBarParams"></PaimBar>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import PaimBar from "@/components/bar/PaimBar";
  import ctitle from "@/components/title/index";
  import liquidfill from "@/components/liquidfill/Liquidfill";
  import CustomizedPie from '@/components/pie/CustomizedPie';
  import depSeach from "@/components/depSeach/index";
  import sjpmBtn from "@/components/sjpmBtn/sjpmBtn";
  import PieChart3D from "@/components/pie/PieChart3D";
  export default {
    name: "mydjk",
    components: {
      ctitle,
      liquidfill,
      CustomizedPie,
      depSeach,
      PaimBar,
      sjpmBtn,
      PieChart3D
    },
    data() {
      return {
        popShow: false,
        curName: '广东省信访局',
        curBmjb:'省级',
        // 参评率
        cplParams:{
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options:{
            radius: "50%",
          }
        },
        // 满意率
        mylParams:{
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options:{
            radius: "50%",
          }
        },
        // 参评情况
        cpqkPieParams: {
          isBr: true,
          labelPer: true,
          data: [],
          angle: 20,
          radius: "35%",
          labelRadius: 25,
          fontSize: this.fontSize(0.16),
          // color:this.$STANDARD.pieColors,
          color: ['rgb( 154, 5, 245 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )','rgb( 235, 131, 124 )'],   //环形颜色
          styleObj: {
            width: "100%",
            height: "100%"
          }
          // canvasStyle: {
          //   width: '100%',
          //   height: '100%'
          // },
          // options: {
          //   colors:['rgb( 154, 5, 245 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )','rgb( 235, 131, 124 )'],   //环形颜色
          //   unti: '件',
          //   labelColor: '#fff',
          //   labelFontSize: 16, //21
          //   labelLineSmooth: 0,
          //   labelLineHeight: 23,
          //   radius: '80%'
          // },
          // data: [],
        },
        // 评价情况
        pjqkPieParams: {
          isBr: true,
          labelPer: true,
          data: [],
          angle: 20,
          radius: "35%",
          labelRadius: 25,
          fontSize: this.fontSize(0.16),
          // color:this.$STANDARD.pieColors,
          color: ['rgb( 154, 5, 245 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )','rgb( 235, 131, 124 )'],   //环形颜色
          styleObj: {
            width: "100%",
            height: "100%"
          }
          // canvasStyle: {
          //   width: '100%',
          //   height: '100%'
          // },
          // options: {
          //   colors:['rgb( 154, 5, 245 )','rgb( 235, 131, 124 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )',],   //环形颜色
          //   unti: '件',
          //   labelColor: '#fff',
          //   labelFontSize: 16, //21
          //   labelLineSmooth: 0,
          //   labelLineHeight: 23,
          //   radius: '80%'
          // },
          // data: [],
        },
        // 信访部门满意率排名
        sjxfBarParams: {
          data: [],
          canvasStyle: {
            width: "80%",
            height: "100%"
          },
        },
      };
    },
    mounted() {
      this.getAPI(this.date, this.curName);
    },
    methods: {
      handlePopShow() {
        this.popShow = true;
      },
      // 点击弹框后触发
      popClick(name,bmjb){
        this.curName=(name === ''?'部门索引':name);
        this.curBmjb=bmjb;
        this.getAPI(this.date, this.curName);
      },
      getAPI(date, xfbm) {
        let xfbmParam=(xfbm === '部门索引' ? 'drop' : xfbm);
        // 1.参评情况-参评率
        get_http("qjywjkMydjkXfbm", {
          gd_id3: '参评率',
          xfbm: xfbmParam,
          day: `[${date.startDate},${date.endDate}]`,
        })
          .then(res => {
            this.cplParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.参评情况-饼图
        get_http("qjywjkMydjkXfbm", {
          gd_id3: '参评情况',
          xfbm: xfbmParam,
          day: `[${date.startDate},${date.endDate}]`,
        })
          .then(res => {
            this.cpqkPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.评价情况-满意率
        get_http("qjywjkMydjkXfbm", {
          gd_id3: '满意率',
          xfbm: xfbmParam,
          day: `[${date.startDate},${date.endDate}]`,
        })
          .then(res => {
            this.mylParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4.评价情况-饼图
        get_http("qjywjkMydjkXfbm", {
          gd_id3: '评价情况',
          xfbm: xfbmParam,
          day: `[${date.startDate},${date.endDate}]`,
        })
          .then(res => {
            this.pjqkPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        this.getBarAPI(date,xfbmParam)
      },
      getBarAPI(date,xfbmParam) {
        // 5.市级信访部门满意率排名
        get_http("qjywjkMydjkXfbm", {
          gd_id3: '满意率排名',
          bmjb:this.curBmjb,
          xfbm: xfbmParam,
          day: `[${date.startDate},${date.endDate}]`,
        })
          .then(res => {
            this.sjxfBarParams.data = res.data.data.sort((a, b) => a.value - b.value);
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      },
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPI(val, this.curName);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .w-seach
      position absolute
      top -78px
      right 113px
      z-index 1000
      box-sizing border-box
      padding-left 42px
      width 270px
      line-height 70px
      font-size 22px
      color #cbfbff
      background url("../../../assets/seach-bg3.png") no-repeat
      background-size 100% 100%
      cursor pointer
      p
        width 150px
        text-align center
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
    .w-popup
      position fixed
      top 0
      left 0
      right 0
      bottom 0
      background rgba(0, 0, 0, 0.4)
      z-index 1005
      font-size 0
    .content
      width 100%
      height 100%
      display flex
      flex-direction row
      padding .2rem .3rem
      box-sizing border-box
      justify-content space-around
      .c-left .c-center .c-right
        display flex
        justify-content center
        box-sizing border-box
      .c-left
        flex 1
        border-right 1px #fff dashed
        display flex
        flex-direction column
        margin-top 0.2rem
        .cpl
          display flex
          flex 1
          justify-content center
          flex-direction center
          position relative
          .cpl-font
            position absolute
            bottom 0.4rem
            text-align center
            color #ffffff
            font-size 0.2rem
        .cpl3d
          width 100%
          div
            width 100%
            height 100%
      .c-center
        flex 1
        display flex
        flex-direction column
        border-right 1px #fff dashed
        margin-top 0.2rem
        .cpl
          display flex
          flex 1
          justify-content center
          flex-direction center
          position relative
          .cpl-font
            position absolute
            bottom 0.4rem
            text-align center
            color #ffffff
            font-size 0.2rem
        .cpl3d
          width 100%
          div
            width 100%
            height 100%
      .c-right
        flex 1
        display flex
        flex-direction column
        .cpl
          display flex
          flex 1
          flex-direction column
          margin-top 0.1rem
        .w-echart
          margin-top 0.3rem
          justify-content center
          display flex
          flex: 1
      </style>
